body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
	font-family: 'Ubuntu';
}
.main {
	display: grid;
	grid-template-columns: 1fr 3fr;
}
.preloader-wrapper {
	height: 100%;
	width: 100%;
	background-color: #fff;
	position: fixed;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	z-index: 100;
}

html, body {
	width: 100%;
	height: 100%;
	min-height: 100%;
}

.wrapper {
	position: fixed;
	width: 300px;
	height: 300px;
	background: #fff;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.wrapper .loader-circle {
	width: 60%;
	height: 60%;
	border-radius: 50%;
	border: 30px solid transparent;
	border-left-color: #4de2ac;
	-webkit-animation: load 0.9s ease-in-out alternate infinite;
	        animation: load 0.9s ease-in-out alternate infinite;
}

.wrapper .loader-bottom {
	position: absolute;
	width: 100%;
	height: 50%;
	top: 50%;
	background: #fff;
	text-align: center;
	color: #4de2ac;
	-webkit-animation: pulse 0.45s linear alternate infinite;
	        animation: pulse 0.45s linear alternate infinite;
}

@-webkit-keyframes load {
	0% {
		-webkit-transform: rotate(-35deg);
		        transform: rotate(-35deg);
	}
	18% {
		border-width: 18px;
	}
	50% {
		border-width: 4px;
	}
	82% {
		border-width: 18px;
	}
	100% {
		-webkit-transform: rotate(215deg);
		        transform: rotate(215deg);
	}
}

@keyframes load {
	0% {
		-webkit-transform: rotate(-35deg);
		        transform: rotate(-35deg);
	}
	18% {
		border-width: 18px;
	}
	50% {
		border-width: 4px;
	}
	82% {
		border-width: 18px;
	}
	100% {
		-webkit-transform: rotate(215deg);
		        transform: rotate(215deg);
	}
}

@-webkit-keyframes pulse {
	0% {
		color: #4de2ac;
	}
	100% {
		color: rgba(77, 226, 172, 0.5);
	}
}

@keyframes pulse {
	0% {
		color: #4de2ac;
	}
	100% {
		color: rgba(77, 226, 172, 0.5);
	}
}
.header {
	background: green;
	padding: 10px 0;
}
.header__container {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin: 0 auto;
	padding-left: 30px;
	padding-right: 30px;
}

.header__cart {
	padding: 11px 24px 11px 20px;
	cursor: pointer;
	background-color: #67C674;
	border-radius: 20px;
	position: relative;
	display: -webkit-flex;
	display: flex;
}
.header__cart-img {
	background: url(/zakaz-dot-ua/static/media/cart.f1b0a00c.svg) no-repeat;
	width: 14px;
	height: 11px;
	position: relative;
	margin: auto 0;
}
.header__cart-img:before {
	content: "";
	position: absolute;
	top: 11.5px;
	bottom: 0;
	right: 0;
	left: 5px;
	width: 2.33px;
	height: 2.33px;
	background: #fff;
	border-radius: 50%;
}
.header__cart-img:after {
	content: "";
	position: absolute;
	top: 11.5px;
	bottom: 0;
	right: 0;
	left: 9.6px;
	width: 2.33px;
	height: 2.33px;
	background: #fff;
	border-radius: 50%;
}
.header__logo {
	background: url(/zakaz-dot-ua/static/media/logo.5a772f5f.svg) no-repeat;
	width: 126px;
	height: 26px;
	margin: auto 0;
}
.header__counter {
	background-color: #FFCF1B;
	width: 24px;
	height: 14px;
	font-size: 8px;
	border-radius: 20px;
	border: 1px solid #67C674;
	position: absolute;
	z-index: 2;
	display: -webkit-flex;
	display: flex;
	top: 6.9px;
	left: 25px;
}
.header__quantity {
	display: block;
	margin: auto;
}
.header__price {
	margin-left: 20px;
	font-size: 14px;
	color: #fff;
}
.sidebar {
	border-right: 1px solid #EFEFEF;
}
.sidebar__title {
	margin: 30px 0 45px 30px;
}
.sidebar__category {
	padding-left: 30px; 
	cursor: pointer;
}
.sidebar__category:hover {
	border-left: 2px solid #45B759;
	color: #45B759;
}
.sidebar__category_active {
	border-left: 2px solid #45B759;
	color: #45B759;
}
.products__item {
	min-width: 176px;
	height: 302px;
	background-color: #fff;
	border: 1px solid #efefef;
	padding: 20px 17px;
	position: relative;
	-webkit-transition: all .25s ease;
	transition: all .25s ease;
}
.products__button {
	display: none;
	position: absolute;
	top:50%; 
	left:50%; 
	-webkit-transform:translate(-50%, -50%); 
	        transform:translate(-50%, -50%); 
	border: none;
	background-color: #43B056;
	border-radius: 20px;
	padding: 12px 40px;
	color: #fff;
	font-size: 18px;
	cursor: pointer;
	-webkit-transition: all .25s ease;
	transition: all .25s ease;
}
.products__item:hover .products__button{
	display: -webkit-flex;
	display: flex;
}
.products__button:hover {
	background-color: #1D8A30;
}
.products__cart-img {
	background: url(/zakaz-dot-ua/static/media/cart.f1b0a00c.svg) no-repeat;
	width: 14px;
	height: 11px;
	position: relative;
	margin: auto 0;
	margin-left: 15px;
}
.products__cart-img:before {
	content: "";
	position: absolute;
	top: 11.5px;
	bottom: 0;
	right: 0;
	left: 5px;
	width: 2.33px;
	height: 2.33px;
	background: #fff;
	border-radius: 50%;
}
.products__cart-img:after {
	content: "";
	position: absolute;
	top: 11.5px;
	bottom: 0;
	right: 0;
	left: 9.6px;
	width: 2.33px;
	height: 2.33px;
	background: #fff;
	border-radius: 50%;
}
.products__price {
	display: block;
	margin-top: 45px;
	font-size: 18px;
}
.products__name {
	margin-top: 10px;
	margin-bottom: 0;
	line-height: 18px;
	max-width: 150px;
	overflow: hidden;
}
.products__price > span {
	font-size: 13px;
}
.products__weight {
	text-align: end;
	color: #B4B4B4;
	font-size: 14px;
	position: absolute;
	bottom: 10px;
	right: 15px;
}
.products__picture{
	height: 150px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
.products__img {
	display: block;
	margin: 0 auto;
}
.products__amount-counter {
	position: absolute;
	top:50%; 
	left:50%; 
	-webkit-transform:translate(-50%, -50%); 
	        transform:translate(-50%, -50%); 
	width: 185px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
.products__button_counter {
	border: 1px solid #45B759;
	border-radius: 50%;
	width: 40px;
	height: 40px;
	background-color: #fff;
	font-size: 18px;
	-webkit-transition: all .25s ease;
	transition: all .25s ease;
}
.products__button_counter:hover {
	color: #fff;
	background-color: #45B759;
}
.products__amount {
	border: 1px solid #EFEFEF;
	width: 80px;
	height: 40px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	border-radius: 20px;
}
.products {
	background-color: #F9FDF9;
}
.products__title {
	margin: 30px;
}
.products__items {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
	margin: 30px;
}
